*, *::before, *::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    margin: 0;
    padding: 0;
    background: #eee;
    font-family: sans-serif;
}


.bgImage{
    position: fixed;
    display: block;
    z-index: -1;
    filter: brightness(40%);
    height: 100%;
    width: 100%;
    object-fit: cover;
}

/********** NAVBAR ***********/

.navbar{
    /***** stay on top *****/
    /*position: sticky; */
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 5px 15px;
    background-image:  linear-gradient(to right, rgb(25, 25, 25), rgba(25, 25, 25, 0.3));
    z-index: 100;
}
.navbar p{
    color: #eee;
    font-size: 20px;
}

.navbarlinks{
    width: 30vw;
    display: flex;
    justify-content: space-between;
}

.navitem{
    margin: auto;
    padding: 0;
    color: #eee;
    font-size: 22px;
}

.navitem p::after{
    content: "";
    display: block;
    height: 2px;
    width: 0px;
    background: rgb(238, 238, 238);
    transition: width ease-out 300ms;
}

.navitem:hover P::after{
    width: 100%;
}

.navitem p:hover{
    color: rgb(194, 194, 194);
    cursor: pointer;
}

@media only screen and (max-width: 1100px){
    /**** Navbar responsive ****/
    /*
    .navbarTitle{
        width: 0px;
        opacity: 0;
        pointer-events: none;
    }*/
    .navbarlinks{
        width: 50vw;
    }
}
/********** Resonsive menu ***********/
@media only screen and (max-width: 600px){

    .menu_hamburger{
        position: absolute;
        top: 8px;
        right: 10px;
        height: 20px;
        width: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .menu_hamburger_1,
    .menu_hamburger_2,
    .menu_hamburger_3{
        height: 2px;
        width: 30px;
        background: white;
        transition: all 1s;
    }
    
    .navbarlinks{
        position: absolute;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        background: rgba(0, 0, 0, 0.8);
        transform: scale(0);
        transform-origin: top right;
        transition: transform .6s;
    }
    .navitem{
        height: 10vh;
        width: 100%;
        display: grid;
        align-content: center;
        justify-content: center;
        transition: all .5s;
    }
    .navitem p{
        font-size: 55px
    }
    .navitem p::after{
        background: transparent;
    }
    .navitem:hover{
        background-color: rgba(255, 255, 255, 0.2);
    }
    .nav_out .navbarlinks{
        transform: scale(1);
    }
    
    /* Hamburger animation */
    .nav_out .menu_hamburger_1{
        transform: translateY(9px) rotateZ(135deg);
    }
    .nav_out .menu_hamburger_2{
        transform: rotateY(90deg);
    }
    .nav_out .menu_hamburger_3{
        transform: translateY(-9px) rotateZ(-135deg);
    }
}



/********** HOME ***********/
.logoicon{
    height: 50px;
    width: 65px;
    padding: 5px 15px;
}

.logolink{
    height: 50px;
    width: 50px;
    padding: 5px 5px;
    margin: 0px 10px;
    border-radius: 30%;
    background-color: rgba(255, 255, 255, 0.25);
    color: rgb(0, 0, 200);
    transition: transform ease-out 300ms;
}

.logolink:hover{
    transform: scale(1.3);
}


/********** Add tooltip ***********/
[tooltip]{
    position: relative;
}

[tooltip]::after{
    position: absolute;
    margin: 0 auto;
    display: block;
    content: attr(tooltip);
    border-radius: 5px;
    padding: 5px;
    background: rgb(0, 0, 105, 0.6);
    width: fit-content;
    color: honeydew;
    
    bottom: -30px;
    left: -50%;

    white-space: nowrap; /*ser till att allt kommer p√• en rad*/
    /*animation*/
    transform: scale(0);
    transition: transform ease-out 400ms;
}
[tooltip]:hover::after{
    transform: scale(1);
}

