.projects{
    text-align: center;
    background-image: linear-gradient(to bottom, rgba(255, 253, 253, 0), #eee);
    min-height: 100vh;
    padding: 20vh 0;
    color: #ddd;
}

.project_card_content a{
    color: rgb(170, 170, 170);
    font-size: 1.4rem;
    text-decoration: none;
}

.project_info{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.project_info p{
    width: 100%;
}

.my_project{
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.project_info div{
    width: 250px;
}

.project_img_container{
    width: 300px;
    height: 250px;
    overflow: hidden;
    /*height: 300px;*/
    border-radius: 25px;
}
.project_img_container img{
    height: 250px;
}
.project_card_content{
    position: relative;
    height: 330px;
    width: 330px;
    margin: 20px;
    transition: transform 0.7s;
    transform-style: preserve-3d;
}
.project_links{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    bottom: 20px;
    color: #eee;
}
.project_links a{
    color: #eee;
    /*text-decoration: overline underline;*/
    padding: 5px;
    border-radius: 10px;
    border: 1px solid rgba(238, 238, 238, 0.6);
    transition: all 0.5s ease-in-out;
}
.project_links a:hover{
    color: #000;
}

.project_card_front, .project_card_back{
    position: absolute;
    height: 330px;
    width: 330px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    border-radius: 5%;
    background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.5), rgba(71, 71, 71, 0.5));
    color: rgb(200, 200, 200);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: transform 1s ease-in-out;
    backface-visibility: hidden;

    /* fix loading if backside when flipping first time */
    animation-name: flipper;
    animation-duration: .3s;
}

.project_card_back{
    transform: rotateY(180deg);
}

.project_card:hover .project_card_back{
    transform: rotateY(0deg);
}
.project_card:hover .project_card_front{
    transform: rotateY(180deg);
}

@keyframes flipper{
    0%  {transform: rotateY(180deg);}
    100% {transform: rotateY(0deg);}
}

